<template>
  <div id="start">
    <div class="content">
      growroomate
    </div>
  </div>
</template>
<script>
import { isLoginRequest } from '../../api/method'
import { isLoginResponse } from '../../api/getData'
export default {
  data () {
    return {

    }
  },
  mounted () {
    // 检验是否登录
    isLoginResponse(this.NAVIGATOR, this)
    let timer = setTimeout(() => {
      isLoginRequest(this.NAVIGATOR, this)
      clearTimeout(timer)
    }, 2000)
    if (this.$root.ISDEBUG) {
      setTimeout(() => {
        window.isLoginResponse(true)
        clearTimeout()
      }, 2000)
    }
  }
}
</script>
<style lang="less" scoped>
@import "../../styles/mixin.less";

#start{
  .wh(100%, 1334px);
  background: url(../../assets/login/login-bg@3x.png) no-repeat;
  background-size: cover;
  .content{
    position: absolute;
    top: 36.7%;
    .wh-l(100%,320px);
    background: url(../../assets/login/矢量智能对象@3x.png) no-repeat;
    background-size: 100% 100%;
    color:#fff;
    font-size: 80px;
  }
}
</style>



